.theme-switch {
  z-index: 5;
  align-items: center;
  margin-right: 50px;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .theme-switch {
    position: absolute;
    top: 25px;
    left: 380px;
  }
}

.theme-switch__text-white {
  color: #fd5103;
  margin: 0 12px 0 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
}

.theme-switch__text-black {
  color: #5f6775;
  margin: 0 0 0 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
}

.theme-switch__control {
  width: 40px;
  height: 20px;
  border: .604701px solid #fd5103;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 16px;
  height: 16px;
  z-index: 2;
  pointer-events: none;
  background-color: #fd5103;
  border-radius: 50%;
  transition: transform .25s linear;
  position: relative;
  transform: translateX(1px);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #fd5103;
  border: .604701px solid #fff;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  background-color: #fff;
  transform: translateX(20px);
}

.theme-switch__toggle:checked + .theme-switch__text-black {
  color: #fd5103;
}

.theme-switch__toggle:checked + .theme-switch__text-white {
  color: #5f6775;
}

